.stand {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 40px;
    padding: 24px;
    background-color: #f5f5f5;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.inputs {
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: 300px;
}

.buttons {
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: 200px;
}

.checkboxes {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 200px;
}
